@import "../../../../styles/variables";

.sets {
  width: 100%;

  input[type='checkbox'] {
    vertical-align: middle; }

  .sets-list {
    margin: 0;
    padding: 0;

    li {
      list-style: none;

      &.expansion {
        margin-left: $ms2 * 1rem; }

      &.legacy-mages {
        margin-left: $ms2 * 2rem; } } }

  .next-level {
    padding-left: $ms2 * 1rem; } }
